
.cart_control[data-v-5fca45b8] {
  font-size: 0;
}
.cart_control .cart-decrease[data-v-5fca45b8] {
  display: inline-block;
  font-size: 24px;
  line-height: 1;
  /* 设置数字，此数字会与当前的字体尺寸相乘来设置行间距 */
  padding: 6px;
  color: #009688;
}
.cart_control .cart-decrease.move-enter[data-v-5fca45b8],
.cart_control .cart-decrease.move-leave-active[data-v-5fca45b8] {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0) rotate(180deg);
          transform: translate3d(24px, 0, 0) rotate(180deg);
  transition: all .4s linear;
  /* 持续 0.4s,匀速过渡 */
}
.cart_control .cart-decrease.move-enter-active[data-v-5fca45b8],
.cart_control .cart-decrease.move-leave[data-v-5fca45b8] {
  opacity: 1;
  transfrom: translate3d(0, 0, 0) rotate(180deg);
  transition: all .4s linear;
  /* all 指的是 transition 之后的样式 */
}
.cart_control .cart-count[data-v-5fca45b8] {
  display: inline-block;
  font-size: 12px;
  width: 12px;
  text-align: center;
  color: black;
  vertical-align: top;
  margin-top: 12px;
}
.cart_control .cart-add[data-v-5fca45b8] {
  display: inline-block;
  font-size: 24px;
  padding: 6px;
  color: #009688;
  line-height: 1;
}



