
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  line-height: 40px;
  height: 40px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  /* flex-grow（定义项目的放大比例）：设为 1， 将等分剩余空间 */
  text-align: center;
  /* 文本对齐方式：居中 */
}
.tab .tab-item a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
.tab .tab-item .item_active {
  color: #009688;
}



